.MoviePlayer {
  position: relative;
  width: 100%;
}

.MoviePlayer::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 300px;
  left: 0;
  bottom: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(32, 37, 41, 1)));
  background: -webkit-linear-gradient(transparent, rgba(32, 37, 41, 1));
  background: -o-linear-gradient(transparent, rgba(32, 37, 41, 1));
  background: linear-gradient(transparent, rgba(32, 37, 41, 1));
  pointer-events: none;
}

.MoviePlayer iframe {
  width: 100%;
  height: 400px;
}



